import React, { Component } from 'react'
import './Skills.css'

class Skills extends Component {

    render() {

        return (
            <main id="skills-main" className="col-3 float-left">
                <h5 className="skills">Skills</h5>
                <h6 className="skills-subheader">Experienced</h6>
                <ul className="experienced skills">
                    <li>Java</li>
                    <li>Python</li>
                    <li>JavaScript</li>
                    <li>ReactJS</li>
                    <li>Flask</li>
                    <li>Android Studio</li>
                    <li>Web Development</li>
                    <li>Web Scraping</li>
                </ul>

                <h6 className="skills-subheader">Familiar</h6>
                <ul className="familiar skills">
                    <li>C</li>
                    <li>C++</li>
                    <li>HTML</li>
                    <li>CSS</li>
                    <li>Linux</li>
                    <li>Git</li>
                    <li>JUnit</li>
                </ul>

                <h6 className="skills-subheader">Exposed</h6>
                <ul className="exposed skills">
                    <li>MySQL</li>
                    <li>jQuery</li>
                    <li>R</li>
                    <li>C#</li>
                    <li>ARM Assembly</li>
                    <li>AWS</li>
                    <li>Unreal Engine</li>
                    <li>Game Development</li>
                    <li>Machine Learning</li>
                </ul>
            </main>
        )
    }
}

export default Skills